import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './index.scss';
import '../../../style/style.scss';
import { Alert, Button, Toast } from 'zzc-design-mobile';

let isClose = false;

export default class App extends Component {

    constructor( props ) {
        super( props );
    }

    defaultBtn() {
        Alert( {
            title: '',
            closable: true
        } );
    }

    titleAlert() {
        Alert( {
            title: '',
            buttons: [
                {
                    text: '',
                    onPress: () => {
                        return false;
                    },
                    props: {
                        style: { color: '#254FCB' }
                    }
                }
            ],
            style: {
                color:'blue'
            },
            className: 'a233'
        } );
    }

    contentAlert() {
        Alert( {
            title: '',
            content: <p>asdadasdas</p>,
            buttons: [
                {
                    text: '',
                    onPress: () => {
                        console.log( '' )
                    },
                    props: {
                        style: { color: '#254FCB' }
                    }
                }
            ]
        } );
    }

    doubleBtnAlert() {
        Alert( {
            title: '',
            content: <p>asdasdasdasd</p>,
            buttons: [
                {
                    text: '',
                    onPress: () => {
                        console.log( '' )
                    },
                    props: {
                        style: { color: '#254FCB' }
                    }
                },
                {
                    text: '',
                    onPress: () => {
                        console.log( '' )
                    },
                    props: {
                        type: 'main',
                        className: 'aaaa'
                    }

                }
            ]
        } );
    }

    maskCloseAlert() {
        Alert( {
            title: '',
            content: <p>asdasdasdasd</p>,
            maskClose: true,
            buttons: [
                {
                    text: '',
                    onPress: () => {
                        console.log( '' )
                    },
                    props: {
                        style: { color: '#254FCB' }
                    }
                },
                {
                    text: '',
                    onPress: () => {
                        console.log( '' )
                    },
                    props: {
                        type: 'main',
                        className: 'aaaa'
                    }

                }
            ]
        } );
    }

    autoCloseAlert() {
        let closeEvent = Alert( {
            title: '',
            buttons: [
                {
                    text: '',
                    onPress: () => {
                        console.log( '' )
                    },
                    props: {
                        style: { color: '#254FCB' }
                    }
                }
            ]
        } );

        setTimeout( () => {
            closeEvent();
        }, 2000 );
    }

    promiseAlert() {
        Alert( {
            title: '',
            maskClose: true,
            buttons: [
                {
                    text: '',
                    onPress: () => new Promise( ( resolve ) => {
                        Toast.info( 'onPress Promise', 1 );
                        setTimeout( resolve, 1000 );
                    } ),
                    props: {
                        type: 'main',
                        className: 'aaaa'
                    }

                }
            ]
        } );
    }

    imgAlert() {
        Alert( {
            title: '',
            content: <div><img src="http://imgcdn50.zuzuche.com/world/base/329/214/556/f89e270b09ef8651fa4fea12867543d5.jpg!/fw/150/quality/100/format/png" /></div>,
            closeCallback: function () { console.log('Alert') },
            buttons: [
                {
                    text: '',
                    onPress: () => {
                        console.log( '' )
                    },
                    props: {
                        style: { color: '#254FCB' }
                    }
                }
            ]
        } );
    }

    render() {
        return (
            <div className="zzc-demo">
                <div className="zzc-demo-header">
                    <h1 className="zzc-demo-title">Alert </h1>
                </div>
                <div className="zzc-demo-body">
                    <Button onClick={this.defaultBtn}></Button>
                    <Button onClick={this.titleAlert}>Alert</Button>
                    <Button onClick={this.contentAlert}>Alert</Button>
                    <Button onClick={this.doubleBtnAlert}>Alert</Button>
                    <Button onClick={this.maskCloseAlert}>maskAlert</Button>
                    <Button onClick={this.autoCloseAlert}>2Alert</Button>
                    <Button onClick={this.promiseAlert}>Alert</Button>
                    <Button onClick={this.imgAlert}>Alert</Button>
                </div>
            </div>

        );
    }
}


